.message {
    position: relative;
    width: 150px;
    height: 70px;
    left: 33%;
    padding-top: 50px;
    padding-bottom: 50px;
}

.imageAndText {
    position: relative;
    width: 250px;
    height: 50px;
}

.imageAndText img {
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 48px;
    border-radius: 50%;
}

.text {
    position: absolute;
    top: 0;
    left: 65px;
    padding: 7px 25px 30px 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    height: 60px;
    background: #0066CC;
    border: none;
    border-radius: 25px 15px 15px 0;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);

}

