.name {
    padding: 5px 5px 0 5px;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
}

.messageText {
    padding: 0 5px 0 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}

.time {
    position: absolute;
    top: 100px;
    left: 15px;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
}