.friendMessage {
    position: relative;
    width: 150px;
    height: 70px;
    left: 4%;
    padding-top: 50px;
    padding-bottom: 50px;
}

.friendImageAndText {
    position: relative;
    width: 250px;
    height: 50px;
}

.friendImageAndText img {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    width: 48px;
    height: 48px;
}

.friendText {
    position: absolute;
    top: 0;
    left: 65px;
    background: #F5F7FB;
    border-radius: 25px 15px 15px 0;
    padding: 7px 25px 30px 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    height: 60px;
    border: none;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
}

.friendText:after {

}

.friendName {
    padding: 5px 5px 0 5px;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
}

.friendMessageText {
    padding: 0 5px 0 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
}

.friendTime {
    position: absolute;
    top: 100px;
    left: 15px;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
}
